
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
data() {
return {
imageURL:require('../../assets/img/1.jpg'), checked: false, value:'',
}
},
components: {
Swipeout, SwipeoutItem, SwipeoutButton
},
beforeCreate(){
this.$store.dispatch('getList')
},
computed:{
getList(){
return this.$store.state.getList
},
moeny(){
if(this.getList){
let moeny = 0
for(let val of this.getList){
val.checked ? moeny += val.nowprice * val.amount * 100 : ''
}
return parseFloat(moeny.toFixed(2))
}
},
imgUrl(){
return this.$store.state.imgUrl
},
},
created(){
document.body.scrollTop = 0
document.documentElement.scrollTop = 0
document.title = '购物车'

this.$store.commit('ACTIVE', 3)
},
activated(){

},
methods:{
plus(id){
let list = { id:id, amount: 1 }
this.$store.dispatch('updateAmount', list)
},
minus(id){
let list = { id:id, amount: -1 }
this.$store.dispatch('updateAmount', list)
},
deletes(id){
this.$store.dispatch('deleteGoods', id)
},
changeAll(){
if(this.checked){
for(let i in this.getList){
this.getList[i].checked = this.checked
}
}
},
onSubmit(){
let array = []
for(let val of this.getList){
val.checked ? array.push(val) : ''
}
if(array.length > 0){
this.$store.dispatch('cartList', array)
this.$router.push({path:'/CreateGoodsCart',query:{ moeny: this.moeny }})
}
},
},
watch:{
getList:{
handler(val,old){
for(let i in val){
if(!val[i].checked){
this.checked = false
break
}else{
this.checked = true
}
}
},deep:true
}
}





}



